
import io from 'socket.io-client';
import { maleArray, femaleArray } from './Names';




let botsocket={};


let battingSelectionCount,bowlingSelectionCount,bowlingTotalCount,battingTotalCount;
const selectionPercentage={
    1:0,
    2:10,
    3:20,
    4:40,
    5:60,
    6:80
}


function randomRungenerator(num){
  
    if(num>=1 && num<=3){
        return [1,2,3,1,2,3,1,2,3,1,2,3,1,2,3,3][Math.floor(Math.random()*15)];
    }
   else{
    return [4,5,6,4,5,6,4,5,6,4,5,6,4,5,6,6][Math.floor(Math.random()*15)];
   }

}
 export function bot(roomId){
    battingSelectionCount={
        1:3,
        2:3,
        3:4,
        4:4,
        5:4,
        6:4,
    }
    bowlingSelectionCount={
        1:1,
        2:1,
        3:1,
        4:1,
        5:1,
        6:1,
    }
     battingTotalCount=22;
     bowlingTotalCount=6;
    let randomValue=Math.random();
    if(botsocket.id!=undefined&&botsocket.id.length>0){
        botsocket.disconnect();
        botsocket.connect();
    }
    else{
        botsocket = io.connect('https://www.handcricket.in/');
       
    }

    let gender=randomValue<0.90?"male":"female";
    let name;
    if(gender=='male'){
       name=  maleArray()[Math.floor(randomValue*maleArray().length)];
    }
    else{
        name= femaleArray()[Math.floor(randomValue*femaleArray().length)];
    }


     botsocket.emit('join-room',name,gender,{roomType:'random',botRoomId:roomId},'Limitless Block');
    setTimeout(()=>{
        
        botsocket.connected&& botsocket.emit('acknowledgement','toss')
    },1000)

  

}
export function botCall(){
    setTimeout(()=>{botsocket.connected&&botsocket.emit('toss-call',Math.random()>0.49?"Heads":"Tails")},(Math.floor(Math.random()*5000) +2000) );
}
export function botChoose(){
    setTimeout(()=>{botsocket.connected&&botsocket.emit("toss-choose",Math.random()>0.49?"Bat":"Bowl")},(Math.floor(Math.random()*6000) +1000) );
}
export function botToss(){
    setTimeout(()=>{botsocket.connected&&botsocket.emit('toss');},Math.floor(Math.random()*6000));
}
export function triggerRun(roomData,playerInput,playerName,botName){

    
    let endPercentage=0;
    let num=null;
    let totalInversePercentage=0;
    let randomValue=Math.random();

    if(roomData.batting!==playerName){

         Object.keys(battingSelectionCount).forEach((key)=>{
           selectionPercentage[key]= 1-(battingSelectionCount[key]/battingTotalCount);
           totalInversePercentage=totalInversePercentage+selectionPercentage[key];
         })

     }
        Object.keys(selectionPercentage).forEach((key)=>{
            
   
                if(roomData.batting===playerName){
                 
                    
                    endPercentage=endPercentage+(bowlingSelectionCount[key]/bowlingTotalCount);
                    
                }
                else{
                    endPercentage=endPercentage+(selectionPercentage[key]/totalInversePercentage);

                }
                
            selectionPercentage[key]=endPercentage;
            if(endPercentage>randomValue&&num==null){

                num=Number(key);
                
            }
        })

        // console.log(selectionPercentage);
        // console.log('batting',battingSelectionCount);
        // console.log('bowling',bowlingSelectionCount);
        
    
        if(roomData.batting===playerName){
            bowlingSelectionCount[playerInput]++;
            bowlingTotalCount++;
        }
        else{
            battingTotalCount++;
             battingSelectionCount[num]++;
             
    
         }
   



    let timeoutSeconds=0;
    
    if((randomValue<0.7 && roomData.timer>6) || randomValue<0.25){
        timeoutSeconds=randomValue*(roomData.timer-1)
    }
    setTimeout(()=>{ botsocket.connected&&botsocket.emit('emit-run-data',num)},timeoutSeconds*1000)
   
}

export function botAnimationDone(){
    botsocket.connected&&botsocket.emit('animation-done');
}